<template>
  <div class="page-component">
    <div class="intro">
      <h2 class="title">Checkbox 多选框组</h2>
      <p class="detail">在一组备选项中进行多选</p>
    </div>

    <div class="intro">
      <h3>基础用法</h3>
      <p>适用于多个勾选框绑定到同一个数组的情景，通过是否勾选来表示这一组选项中选中的项。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-checkBoxGroup v-model="anotherValue">
          <dog-checkBox class="checkBox" value="杨超越">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">杨超越</template>
              <template slot="tip-part">
                <img src="../../chaoyue.jpg" style="width: 300px" alt="杨超越">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="小姐姐">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">小姐姐</template>
              <template slot="tip-part">
                <img src="../../111.jpg" style="width: 300px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="单身狗">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">单身狗</template>
              <template slot="tip-part">
                <img src="../../dog.jpg" style="width: 100px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-button size="mini" @click.stop="anotherValue = ['小姐姐','杨超越','单身狗']">全都要</dog-button>
        </dog-checkBoxGroup>
      </div>
    </div>

    <div class="intro">
      <h3>单个用法</h3>
      <p>单独使用可以表示两种状态之间的切换，写在标签中的内容为 checkbox 按钮后的介绍。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-checkBox class="checkBox" v-model="value" :value="1">备选项</dog-checkBox>
      </div>
    </div>

    <div class="intro">
      <h3>禁用用法</h3>
    </div>
    <div class="card">
      <div class="box">
        <dog-checkBoxGroup v-model="otherValue">
          <dog-checkBox class="checkBox" value="杨超越">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">杨超越</template>
              <template slot="tip-part">
                <img src="../../chaoyue.jpg" style="width: 300px" alt="杨超越">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="小姐姐">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">小姐姐</template>
              <template slot="tip-part">
                <img src="../../111.jpg" style="width: 300px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="单身狗">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">单身狗</template>
              <template slot="tip-part">
                <img src="../../dog.jpg" style="width: 100px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" v-model="value" value="学习" disabled>学习</dog-checkBox>
        </dog-checkBoxGroup>
      </div>
    </div>

    <div class="intro">
      <h3>可选项目数量的限制</h3>
      <p>使用 min 和 max 属性能够限制可以被勾选的项目的数量。</p>
    </div>
    <div class="card">
      <p>例子使用最小为1,最大为2。同时手动设置全选中无效(此例中最大选项为3,大于2)</p>

      <div class="box">
        <dog-checkBoxGroup v-model="minmaxValue" :min="1" :max="2">
          <dog-checkBox class="checkBox" value="杨超越">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">杨超越</template>
              <template slot="tip-part">
                <img src="../../chaoyue.jpg" style="width: 300px" alt="杨超越">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="小姐姐">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">小姐姐</template>
              <template slot="tip-part">
                <img src="../../111.jpg" style="width: 300px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-checkBox class="checkBox" value="单身狗">
            <dog-hover-tip type="right-center">
              <template slot="hover-part">单身狗</template>
              <template slot="tip-part">
                <img src="../../dog.jpg" style="width: 100px" alt="示意图">
              </template>
            </dog-hover-tip>
          </dog-checkBox>
          <dog-button size="mini" @click.stop="anotherValue = ['小姐姐','杨超越','单身狗']">全都要</dog-button>
        </dog-checkBoxGroup>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      value: true,
      anotherValue: ["杨超越"],
      minmaxValue: ["杨超越"],
      otherValue: ["学习"]
    };
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/page.scss";
</style>