<template>
  <div class="page-component">
    <div class="intro">
      <h2 class="title">MessageBox 弹框</h2>
      <p class="detail">模态对话框组件，用于消息提示、确认消息和提交内容。</p>
    </div>

    <div class="intro">
      <h3>消息提示</h3>
      <p>当用户进行操作时会被触发，该对话框中断用户操作，直到用户确认知晓后才可关闭。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-message-btn">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>确认消息</h3>
      <p>提示用户确认其已经触发的动作，并询问是否进行此操作时会用到此对话框。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-message-btn" @click.stop="handleClick('confirm')">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>自定义</h3>
      <p>可自定义配置不同内容。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-message-btn" @click.stop="handleClick('custom-content')">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>区分取消与关闭</h3>
      <p>有些场景下，点击取消按钮与点击关闭按钮有着不同的含义。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-message-btn" @click.stop="handleClick('custom')">点击打开</dog-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleClick(selector) {
      const _self = this;
      switch (selector) {
        case "alert":
          this.$alert({
            title: "标题名称",
            content: "这是一段内容"
          });
          break;
        case "confirm":
          this.$confirm({
            title: "提示",
            content: "此操作不可撤回，确定么?"
          });
          break;
        case "custom-content":
          this.$confirm({
            title: "自定义提示",
            content: `<h1 style="color: red;">自定义HTML</h1>`
          });
          break;
        case "custom":
          this.$confirm({
            title: "自定义提示",
            content: "自定义内容",
            confirmButtonText: "保存",
            cancelButtonText: "放弃"
          });
          break;
      }
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/page.scss";
</style>