<template>
  <div class="page-component">
    <div class="intro">
      <h2 class="title">Select 选择器</h2>
      <p class="detail">当选项过多时，使用下拉菜单展示并选择内容</p>
    </div>

    <div class="intro">
      <h3>基础用法</h3>
      <p>适用广泛的基础单选</p>
    </div>
    <div class="card">
      <div class="box" style="width:200px;">
        <dog-select v-model="inputValue">
          <dog-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >{{ item.label }}</dog-option>
        </dog-select>
      </div>
    </div>

    <div class="intro">
      <h3>有禁用选项</h3>
      <p>选项中有选项不能选择</p>
    </div>
    <div class="card">
      <div class="box" style="width:200px;">
        <dog-select>
          <dog-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled"
          >{{ item.label }}</dog-option>
        </dog-select>
      </div>
    </div>

    <div class="intro">
      <h3>禁用选择器</h3>
    </div>
    <div class="card">
      <div class="box" style="width:200px;">
        <dog-select disabled v-model="inputValue">
          <dog-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          >{{ item.label }}</dog-option>
        </dog-select>
      </div>
    </div>

    <div class="intro">
      <h3>多选选项</h3>
    </div>
    <div class="card">
      <p>多选展示</p>
      <div class="box" style="width:200px;">
        <dog-select multiple v-model="multipleInputValue">
          <dog-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled"
          >{{ item.label }}</dog-option>
        </dog-select>
      </div>

      <p>多选省略展示</p>
      <div class="box" style="width:200px;">
        <dog-select multiple collapse-tags v-model="multipleInputValue">
          <dog-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
            :disabled="item.disabled"
          >{{ item.label }}</dog-option>
        </dog-select>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      options: [
        {
          value: "选项1",
          label: "黄金糕"
        },
        {
          value: "选项2",
          label: "双皮奶"
        },
        {
          value: "选项3",
          label: "蚵仔煎"
        },
        {
          value: "选项4",
          label: "龙须面"
        },
        {
          value: "选项5",
          label: "北京烤鸭",
          disabled: true
        }
      ],
      inputValue: "选项2",
      multipleInputValue: ["选项2"]
    };
  },
  methods: {}
};
</script>
<style lang="scss">
@import "@/assets/styles/page.scss";
.demo-row-content {
  .select-wrapper {
    width: 240px;
    &:not(:first-child) {
      margin-top: 16px;
    }
  }
}
</style>