<template>
  <div class="page-component">
    <div class="intro">
      <h2 class="title">Input 输入框</h2>
      <p class="detail">通过鼠标或键盘输入字符</p>
    </div>
    <div class="intro">
      <h3>基础用法</h3>
    </div>
    <div class="card">
      <p>基础用法</p>
      <div class="box">
        <dog-input placeholder="请输入内容" v-model="inputValue"/>
      </div>
      <p>
        输入内容为：
        <span class="bold">{{ inputValue }}</span>
      </p>
    </div>

    <div class="intro">
      <h3>禁用状态</h3>
      <p>禁用该组件。</p>
    </div>
    <div class="card">
      <p>禁用状态</p>
      <div class="box">
        <dog-input disabled v-model="inputValue" placeholder="请输入内容"/>
      </div>
    </div>

    <div class="intro">
      <h3>带icon的输入框</h3>
      <p>带有图标标记输入类型</p>
    </div>
    <div class="card">
      <p>prefix 输入框前</p>
      <div class="box">
        <dog-input placeholder="请输入内容" prefix-icon="search"/>
      </div>
      <p>suffix 输入框后</p>
      <div class="box">
        <dog-input placeholder="请输入内容" suffix-icon="calendar-alt"/>
      </div>
    </div>

    <div class="intro">
      <h3>复合型的输入框</h3>
      <p>可前置或后置元素，一般为标签或按钮</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-input placeholder="请输入内容">
          <template slot="prepend">
            <div class="prepend-part">Http://</div>
          </template>

          <template slot="append">
            <div class="append-part">.com</div>
          </template>
        </dog-input>
      </div>
    </div>

    <div class="intro">
      <h3>文本域</h3>
      <p>用于输入多行文本信息，通过将 type 属性的值指定为 textarea</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-input type="textarea" placeholder="请输入内容"/>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      inputValue: "hello world"
    };
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/_var.scss";
@import "@/assets/styles/page.scss";

.prepend-part,
.append-part {
  display: block;
  padding: 0 $s-offset;
  height: 32px;
  line-height: 32px;
  background-color: #fafafa;
  border: 1px solid $main-color;
  border-radius: $base-radius 0 0 $base-radius;
}
.append-part {
  border-radius: 0 $base-radius $base-radius 0;
}
</style>