<template>
  <div class="page-component">
    <div class="intro">
      <h2 class="title">Message 消息提示</h2>
      <p class="detail">常用于主动操作后的反馈提示。</p>
    </div>

    <div class="intro">
      <h3>基础用法</h3>
      <p>从顶部出现，2 秒后自动消失。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-toast-btn" @click.stop="handleClick()">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>不同状态</h3>
      <p>用来显示「成功、警告、消息、错误」类的操作反馈。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-toast-btn" @click.stop="handleClick('success')">成功</dog-button>
        <dog-button class="demo-toast-btn" @click.stop="handleClick('warn')">警告</dog-button>
        <dog-button class="demo-toast-btn" @click.stop="handleClick('info')">消息</dog-button>
        <dog-button class="demo-toast-btn" @click.stop="handleClick('error')">错误</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>使用HTML片段</h3>
      <p>content属性支持传入 HTML 片段。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-toast-btn" @click.stop="handleClick('info', 'html')">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>自定义垂直偏移量</h3>
      <p>用户自定义Message组件的位置。</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-toast-btn" @click.stop="handleClick('info', '', 64)">点击打开</dog-button>
      </div>
    </div>

    <div class="intro">
      <h3>显示关闭按钮</h3>
      <p>可以自己进行关闭</p>
    </div>
    <div class="card">
      <div class="box">
        <dog-button class="demo-toast-btn" @click.stop="handleClickClose('info', '', 64)">点击打开</dog-button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    handleClick(type = "info", text = "text", verticalOffset = 32) {
      const content =
        text !== "html" ? "提示内容" : `<p style="color: red;">提示内容</p>`;
      this.$message({
        type,
        content,
        verticalOffset,
        onClose() {
          console.log("close");
        }
      });
    },
    handleClickClose(type = "info", text = "text", verticalOffset = 32) {
      const content =
        text !== "html" ? "提示内容" : `<p style="color: red;">提示内容</p>`;
      this.$message({
        type,
        content,
        verticalOffset,
        showClose: true,
        onClose() {
          console.log("close");
        }
      });
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/styles/page.scss";
</style>